import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/map/:subscriptionKey/:latitude/:longitude/:tilesetId/:statesetId/:company',
    // path: '/map',
    name: 'Map',
    component: () => import('../views/Map.vue'),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
