    // 2. Upload a Drawing package
    async dwgFile(event) {
      if (event.target.files[0]) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.file = e.target.result;
        };
        reader.readAsArrayBuffer(event.target.files[0]);
        this.uploadDwgBtn = true;
      }
    },
    async uploadDwg() {
      this.isLoading = true;
      const formData = new FormData();
      formData.append('file', this.file);
      axios({
        method: 'post',
        url: `https://atlas.microsoft.com/mapData/upload?api-version=1.0&dataFormat=zip&subscription-key=${this.subscriptionKey}`,
        data: this.file,
        headers: {
          'Content-Type': 'application/octet-stream',
        },
      })
        .then((response) => {
          // console.log(`headers: ${JSON.stringify(response.headers)}`);
          // console.log(`Step 2 location: ${response.headers.location}`);
          this.mapDataOperationsUrl = response.headers.location;
          // console.log(`2. mapDataOperationsUrl: ${this.mapDataOperationsUrl}`);
